.object-wrap {
  display: inline-block;
  width: 370px;
  height: 365px;
  position: relative;
}

.content-img-cntnr {
  width: 370px;
  height: 305px;
}

.object__image {
  width: 370px;
  height: 305px;
  border-radius: 5px;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.content-img-filter {
  width: 100%;
  height: 305px;
  background-color: rgba(0, 0, 0, 0.1);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

